import Vue from 'vue';
import Router from 'vue-router';
import routes from './all';
Vue.use(Router);
routes.unshift({path: '/', redirect: '/home'});
export default new Router({
  mode: 'history',
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else return {x: 0, y: 0};
  },
  routes
});



